<template>
  <div class="HeaderBar">
  <div class="left">
    <div :class="logoStyle">
       <i class="anticon anticon-github" style="font-size: 24px;"></i>
    </div>
  </div>
  <div class="right">
    <div>
      <i  class="anticon anticon-menu-fold" style="font-size: 20px;" @click="click2Collapse"></i>
    </div>
    <div>
      <span v-text="nowTime"></span>
      <span>admin 欢迎您登录！</span>
    </div>
  </div>
</div>
</template>

<script>
import { mapActions, mapGetters } from 'vuex'
export default {
    name: 'HeaderBar',
    data () {
        return {
            nowTime: this.$util.getNowTime()
        }
    },
    created () {
        setInterval(() => {
            this.nowTime = this.$util.getNowTime()
        }, 1000)
    },
    computed: {
    // 计算属性里面取 state 的值
        ...mapGetters({
            vx_get_isCollapse: 'vx_get_isCollapse'
        }),
        logoStyle () {
            if (this.vx_get_isCollapse) {
                return 'logo-min'
            } else {
                return 'logo'
            }
        }
    },
    methods: {
        ...mapActions(['vx_set_isCollapse']), // 映射方法
        click2Collapse () {
            this.vx_set_isCollapse(!this.vx_get_isCollapse)
        }

    },
    watch: {

    }
}
</script>

<style scoped lang="less">
.HeaderBar {
  width: 100%;
  height: 50px;
  position: fixed !important;
  z-index: 999;
  background: #1976d2;
  color: #fff;
  box-shadow: 0 2px 5px 0 rgba(0, 0, 0, 0.5);
  display: flex;
  align-items: center;
  justify-content: space-between;
  .right {
    display: flex;
    width: 100%;
    align-items: center;
    justify-content: space-between;
  }
  .logo {
    width: 210px;
    height: 50px;
    line-height: 50px;
    text-align: center;
    transition: 0.48s;
  }
  .logo-min {
    width: 64px;
    height: 50px;
    line-height: 50px;
    text-align: center;
    transition: 0.48s;
  }
}
i.anticon {
  cursor: pointer;
}
</style>




