<template>
  <div class="layout">
      <HeaderBar></HeaderBar>
      <div class="content">
          <div class="contentMune">
              <MunuBar :defaultActive="defaultActive"></MunuBar>
          </div>
          <div class="contentView">
              <!-- 面包 -->
              <div class="breadNav">
                   <ul class="breadList">
                        <li :key="index" v-for="(item, index) in vx_get_breadList" 
                            @contextmenu.prevent="clickKeyRight($event, item, index)" 
                            :class="item.selected?'active':''"
                            @click="clickBreadListItem(item, index)">
                            <span v-text="item.text"></span>
                            <span class="btn-close" @click.stop="clickCloseBreadListItem($event, item, index)">×</span>
                        </li>
                    </ul>
              </div>
              <div></div>
              <!-- 路由 -->
              <router-view></router-view>
          </div>
      </div>
        <clickKeyRightView  
            v-show="isShowRightClickMenu" 
            v-clickoutside="closeRightClickMenu"  
            @click-menu="clickRightMenu">
        </clickKeyRightView>
  </div>
</template>
<script>
import HeaderBar from './hearderBar'
import MunuBar from './menuBar'
import clickKeyRightView from './clickKeyRightView'
import Clickoutside from 'element-ui/src/utils/clickoutside'

import { mapGetters, mapActions } from 'vuex'

export default {
    name: 'layout',
    components: {
        HeaderBar,
        MunuBar,
        clickKeyRightView
    },
    directives: {
        Clickoutside
    },
    data () {
        return {
            isShowRightClickMenu: false,
            rightClickMenu: null,
            defaultActive: ''
        }
    },
    computed: {
        ...mapGetters({
            vx_get_breadList: 'vx_get_breadList'
        })
    },
    created () {
        console.log(this.$util.getNowTime())
    },
    methods: {
        ...mapActions([
            'vx_set_breadList'
        ]),
        clickKeyRight (event, item, index) {
            if (event.button === 2) {
                this.rightClickMenu = item
                let rightClickMenu = document.getElementById('right-click-menu')
                rightClickMenu.style.top = event.pageY + 'px'
                rightClickMenu.style.left = event.pageX + 'px'
                this.isShowRightClickMenu = true
            }
        },
        closeRightClickMenu () {
            this.isShowRightClickMenu = false
        },
        clickRightMenu (index) {
            if (index === 0) {
                // 不允许直接修改 Vuex 中的 state 所以深复制出来处理
                let breadList = JSON.parse(JSON.stringify(this.vx_get_breadList))
                breadList.splice(index, 1)
                this.vx_set_breadList(breadList)
            } else if (index === 1) {
                this.vx_set_breadList([this.rightClickMenu])
            } else if (index === 2) {
                this.vx_set_breadList([])
            }
            this.isShowRightClickMenu = false
        },
        // 面包屑导航到路由
        clickBreadListItem (item, index) {
            let breadList = JSON.parse(JSON.stringify(this.vx_get_breadList))
            if (breadList.length > 1) {
                breadList.map(itemDto => {
                    if (itemDto.text === item.text) {
                        itemDto.selected = true
                    } else {
                        itemDto.selected = false
                    }
                })
                this.vx_set_breadList(breadList)
            }
            this.defaultActive = item.defaultActive
            this.$router.push(item.name)
        },
        // 删除面包屑
        clickCloseBreadListItem ($event, item, index) {
            // 如果面包屑只有一个导航到空
            if (this.vx_get_breadList.length === 1) {
                this.$router.push('/')
                this.vx_set_breadList([])
                this.defaultActive = null
            } else {
                let breadList = JSON.parse(JSON.stringify(this.vx_get_breadList))
                breadList.splice(index, 1)
                // 如果删除为选中则选中提前一个
                if (item.selected) {
                    breadList[index - 1].selected = true
                    if (breadList.length === index) {
                        breadList[index - 1].selected = true
                        this.defaultActive = breadList[index - 1].defaultActive
                        this.$router.push(`/${this.vx_get_breadList[index - 1].name}`)
                    } else {
                        breadList[index].selected = true
                        this.defaultActive = breadList[index].defaultActive
                        this.$router.push(`/${this.vx_get_breadList[index].name}`)
                    }
                }
                this.vx_set_breadList(breadList)
            }
        }
    },
    watch: {

    }
}
</script>
<style lang="less" scoped>
.layout {
  width: 100%;
  height: 100%;
  position: relative;
  .content {
    position: absolute;
    top: 52px;
    left: 0;
    display: flex;
    width: 100%;
    height: 100%;
    .contentMune {
      height: 100%;
    }
    .contentView {
      margin: 5px 5px;
      height: 100%;
      width: 100%;
      background: #fdfdfd;;
      .breadNav {
        background-color: #ededed;
        height:40px;
        .breadList {
          list-style: none;
          margin: 0;
          padding: 0;
          overflow: auto;
          width: auto;
          zoom: 1;
          background-color: rgb(243, 246, 249);
          li {
            cursor: pointer;
            border-radius: 4px;
            float: left;
            padding: 10px 25px 10px 10px;
            margin-right: 2px;
            position: relative;
            font-family: Arial, Microsoft YaHei, sans-serif;
            font-size: 13px;
            text-align: left;
            border-right: 1px solid #dadada;
            color: #888;
            margin-bottom: 4px;
            background: #e3e3e3
          }
          & .active {
            background: #1976d2e3;
            color: #fff;
          }
          & .active .btn-close {
            opacity: 1;
          }
          & :hover {
            cursor: pointer;
          }
          & :hover .btn-close {
            opacity: 1;
          }
          & .btn-close {
            position: absolute;
            display: inline-block;
            opacity: 0.5;
            top: 5px;
            right: -6px;
            line-height: 10px;
            display: inline-block;
            width: 18px;
            height: 18px;
            border-radius: 14px;
            font-size: 16px;
            color: white;
            transition: all 0.2s ease;
          }
          & .btn-close:hover {
            color: #fff;
            transform: scale(1.5);
            cursor: pointer;
          }
        }
      }
    }
  }
}
</style>



