import * as type from '../mutation_types'

const state = {
    breadList: [],
    isCollapse: false
}

const getters = {
    vx_get_breadList: state => state.breadList,
    vx_get_isCollapse: state => state.isCollapse
}
const actions = {
    vx_set_isCollapse ({ commit }, isCollapse) {
        commit(type.SET_ISCOLLAPSE, { isCollapse })
    },
    vx_set_breadList ({ commit }, breadList) {
        commit(type.SET_BREADLIST, { breadList })
    }
}

const mutations = {
    [type.SET_ISCOLLAPSE] (state, { isCollapse }) {
        state.isCollapse = isCollapse
    },
    [type.SET_BREADLIST] (state, { breadList }) {
        state.breadList = breadList
    }
}

export default {
    state,
    getters,
    actions,
    mutations
}



