
import Vue from 'vue'
import Router from 'vue-router'
import storePay from '../page/store-manage/store-pay'
import clubCard from '../page/store-manage/club-card'
import clubManage from '../page/store-manage/club-manage'
import deliveryWay from '../page/store-manage/delivery-way'
Vue.use(Router)
/** 路由赖加载配置 **/
// const Login = resolve => require(['../page/login/login.vue'], resolve)
// const Hello = resolve => require(['../components/Hello.vue'], resolve)
/** 路由赖加载配置 **/
// const storePay = resolve => require(['page/store-manage/store-pay.vue'], resolve)

export default new Router({
    routes: [
    { path: '/' },
    { path: '*' },
    { path: '/store-pay', name: 'store-pay', component: storePay },
    { path: '/club-card', name: 'club-card', component: clubCard },
    { path: '/club-manage', name: 'club-manage', component: clubManage },
    { path: '/delivery-way', name: 'delivery-way', component: deliveryWay }
    ]
})



