<template>
<div class="munuBar">
<el-menu class="el-menu-vertical-demo"
    :default-active="defaultActive"
    @select="selectSubMenu"
    @open="handleOpen" 
    @close="handleClose" 
    :collapse="vx_get_isCollapse" 
    unique-opened 
    style="background-color:#fafafa">
  <template v-for="(item,index) in ItemList">
    <menuList :key="index" :item="item" :navIndex="String(index)"></menuList>
  </template>
</el-menu>
</div>
</template>
<script>
import { mapGetters, mapActions } from 'vuex'
import menuList from './menuList.vue'
import ItemList from './menuList.json'
export default {
    name: 'menuBar',
    components: {
        menuList
    },
    props: {
        defaultActive: String
    },
    data () {
        return {
            clickSubItemName: ''
        }
    },
    computed: {
    // 计算属性里面取 state 的值
        ...mapGetters({
            vx_get_isCollapse: 'vx_get_isCollapse',
            vx_get_breadList: 'vx_get_breadList'
        })
    },
    created () {
        this.ItemList = ItemList.data.children
    },
    methods: {
    // 把方法映射出来
        ...mapActions(['vx_set_isCollapse', 'vx_set_breadList']),
        handleOpen (key, keyPath) {
      // console.log(key, keyPath)
        },
        handleClose (key, keyPath) {
      // console.log(key, keyPath)
        },
      // 点击选择菜单的时候
        selectSubMenu (index, indexPath) {
            let breadList = JSON.parse(JSON.stringify(this.vx_get_breadList))
            console.log(index, indexPath)
            let clickSubIndex = indexPath[indexPath.length - 1].split('-')
            let clickSubMenuParent = this.ItemList[indexPath[0]]
            let clickSubItem = clickSubMenuParent.children[clickSubIndex[clickSubIndex.length - 1]]
            clickSubItem.defaultActive = index
            if (breadList.length <= 0) {
                clickSubItem.selected = true
                clickSubItem.defaultActive = index
                breadList.push(clickSubItem)
            } else {
                if (!this.$util.contains(breadList, 'text', clickSubItem.text)) {
                    breadList.map(item => {
                        item.selected = false
                    })
                    clickSubItem.selected = true
                    clickSubItem.defaultActive = index
                    breadList.push(clickSubItem)
                } else {
                    breadList.map(item => {
                        if (item.text === clickSubItem.text) {
                            item.selected = true
                        } else {
                            item.selected = false
                        }
                    })
                }
            }
            this.vx_set_breadList(breadList)
            this.$router.push(clickSubItem.name)
        },
        handlerBreadList (item) {
            let breadList = JSON.parse(JSON.stringify(this.vx_get_breadList))
            if (breadList.length > 0) {
                if (!this.$util.contains(breadList, 'text', item.text)) {
                    breadList.push(item)
                }
                this.vx_set_breadList(breadList)
            } else {
                breadList.push(item)
                this.vx_set_breadList(breadList)
            }
        }
    }
}
</script>

<style lang="less">
.munuBar {
  height: 100%;
  background-color: #fafafa;
  transition: 0.2s;
  z-index: 998;
  border-right: 1px solid #e3e3ee;
}
.munuBar::-webkit-scrollbar {
  display: none;
}
.el-menu {
  background-color: white;
}
.el-menu i {
  font-size: 14px;
}
.el-menu--collapse i:first-child {
  font-size: 21px;
}
.el-submenu .el-menu {
  background-color: rgb(240, 240, 240);
}
.el-menu-vertical-demo:not(.el-menu--collapse) {
  transition: width 0.2s cubic-bezier(0.35, 0, 0.25, 1),
    translate 0.2s cubic-bezier(0.35, 0, 0.25, 1);
  background-color: white;
  width: 200px;
  min-height: 400px;
}
.el-menu-item.is-active {
  color: #23b7e5;
}
.el-menu .active {
  color: #23b7e5;
}
.el-menu-item,
.el-submenu__title {
  line-height: 45px;
  height: 45px;
}
.el-submenu .el-menu-item {
  line-height: 45px;
  height: 45px;
}
</style>


